import React, { Component } from 'react';
import { Chart } from 'react-google-charts';

const StatusPieChart = () => {
    const options = {

        legend: {
            position: 'bottom', maxLines: 4, textStyle: {
                color: 'black',
                fontName: 'Arial',
                fontSize: '11'
            }
        },

        title: 'Status Chart',
    };

    return (
        <div>
            <Chart
                style={{ width: "500px", height: "500px" }}
                chartType="PieChart"
                loader={<div>Loading Chart</div>}
                data={[
                    ['Status', 'Count'],
                    ['Approved', 5],
                    ['Submitted', 2],
                    ['Cancelled', 2],
                    ['Pending', 3],
                ]}
                options={options}
            />
        </div>
    );
}

export default StatusPieChart;