import React, { Component } from 'react';
import ReactTable from 'react-table-v6'
import 'react-table-v6/react-table.css'
import * as AppConstants from '../Common/AppConstants'

const ProductsDemoGrid = (props) => {

    const { ProductsData, ...actions } = props;

    let columns = [
        {
            Header: "First Name",
            accessor: "firstName",
            sortable: false,
            show: true,
            displayValue: " First Name"
        },
        {
            Header: "Status",
            accessor: "status",
            sortable: false,
        },
        {
            Header: "Visits",
            accessor: "visits",
            sortable: false,
        }
    ];

    return (
        <div style={{ zIndex: 0, overflowY: "visible" }}>
            <ReactTable
                data={ProductsData}
                columns={columns}
                defaultPageSize={AppConstants.DEFAULT_GRID_PAGE_SIZE}
            />
        </div>
    );

}

export default ProductsDemoGrid;