import React, { Component } from 'react';
import Autosuggest from 'react-autosuggest';
import '../styles/auto-suggest.css'
import ProductsGrid from './ProductsGrid';
import StatusPieChart from './StatusPieChart';
import ProductsDemoGrid from './ProductsDemoGrid';


const originalData = [
    { firstName: "azxa", status: "Pending", visits: 155 },
    { firstName: "cabyFaa", status: "Pending", visits: 155 },
    { firstName: "daxAaaa", status: "Approved", visits: 1785 },
    { firstName: "eAvwa", status: "Approved", visits: 175 },
    { firstName: "fdaSauaa", status: "Cancelled", visits: 165 },
    { firstName: "gasataa", status: "Cancelled", visits: 157 },
    { firstName: "hwesaaewea", status: "Approved", visits: 153 },
    { firstName: "iaraaa", status: "Submitted", visits: 155 },
    { firstName: "jaqeweaa", status: "Pending", visits: 1555 },
    { firstName: "kapbFaa", status: "Submitted", visits: 155 },
    { firstName: "ldoaAAadsdweaa", status: "Approved", visits: 17585 },
    { firstName: "mnAaaaa", status: "Approved", visits: 175 }
]

class AutoSuggestSearch extends Component {

    constructor(props) {
        super(props);
        this.state = {
            value: '',
            suggestions: [],
            ProductsData: originalData,
            data: originalData,
        };
    }

    onSuggestionsFetchRequested = ({ value }) => {
        this.setState({
            suggestions: this.getSuggestions(value)
        });
    };

    onSuggestionsClearRequested = () => {
        this.setState({
            suggestions: []
        });
    };

    getSuggestions = value => {
        const inputValue = value.trim().toLowerCase();
        const inputLength = inputValue.length;

        return inputLength === 0 ? [] : this.state.ProductsData.filter(lang =>
            lang.firstName.toLowerCase().includes(inputValue.toLowerCase()) ||
            lang.status.toLowerCase().includes(inputValue.toLowerCase())
        );
    };


    handleChange = event => {
        this.setState({ value: event.target.value }, () => {
            this.globalSearch();
        });
    };

    globalSearch = () => {
        let { value } = this.state;

        // let findValues = this.find(value)
        // console.log(findValues)


        let filteredData = originalData.filter(item => {
            // let temp = this.matchRuleShort(value.toLowerCase(), item.firstName.toLowerCase())
            return (
                this.matchRuleShort(value.toLowerCase(), item.firstName.toLowerCase()) ||
                this.matchRuleShort(value.toLowerCase(), item.status.toLowerCase()) ||
                item.firstName.toLowerCase().includes(value.toLowerCase()) ||
                item.status.toLowerCase().includes(value.toLowerCase()) ||
                item.visits
                    .toString()
                    .toLowerCase()
                    .includes(value.toLowerCase())
            );
        });
        this.setState({ data: filteredData });
    };

    matchRuleShort = (str, item) => {
        return new RegExp('^' + str.replace(/\*/g, '.*') + '$').test(item);
    }

    find = (regex) => {
        var matches = originalData.filter(e => { return regex.test(e.firstName) });
        return matches.map(function (e) { return originalData.indexOf(e) });
    }

    render() {

        const { value, suggestions } = this.state;

        // Autosuggest will pass through all these props to the input.
        const inputProps = {
            placeholder: 'Search .....',
            value,
            onChange: this.handleChange
        };

        const renderSuggestion = suggestion => {
            return (<div>
                {suggestion.firstName}
            </div>)
        }
        const getSuggestionValue = (suggestion) => {
            return suggestion.firstName;
        };
        const onSuggestionSelected = (event, { suggestion, suggestionValue, suggestionIndex, sectionIndex, method }) => {
            this.setState({ value: suggestion.firstName })
        }

        return (
            <div style={{ padding: '10px' }}>
                <Autosuggest
                    className="form-control"
                    suggestions={suggestions}
                    onSuggestionsFetchRequested={this.onSuggestionsFetchRequested}
                    onSuggestionsClearRequested={this.onSuggestionsClearRequested}
                    getSuggestionValue={getSuggestionValue}
                    renderSuggestion={renderSuggestion}
                    inputProps={inputProps}
                    onSuggestionSelected={onSuggestionSelected}
                />
                <div style={{ display: "flex", width: "100%", justifyContent: "space-between" }}>
                    <div style={{ width: "70%" }}>
                        <ProductsDemoGrid
                            ProductsData={this.state.data}
                        />
                    </div>
                    <StatusPieChart />
                </div>
            </div >
        );
    }
}

export default AutoSuggestSearch;
